<template>
  <div>
    <van-form @submit="handleSubmit" :show-error-message="false">
      <!--  -->
      <van-field class="user-type" v-model="userType" name="userType" label="" center>
        <template #input>
          <base-switch v-model="isChild"></base-switch>
        </template>
      </van-field>
      <!--  -->
      <van-field
        name="patName"
        v-model="patName"
        label=""
        label-width="120"
        :placeholder="isChild ? '' : ''"
        :rules="[{ required: true, message: '' }]"
        clearable
      ></van-field>
      <!--  -->
      <van-field name="patSex" label="" label-width="120" v-if="isChild">
        <template #input>
          <van-radio-group v-model="patSex" direction="horizontal">
            <van-radio name="1"></van-radio>
            <van-radio name="2"></van-radio>
          </van-radio-group>
        </template>
      </van-field>
      <!--  -->
      <div v-if="isChild">
        <van-field
            name="patBirthday"
            v-model="patBirthday"
            label=""
            label-width="120"
            placeholder=""
            @click-input="handleSelectDate"
            :rules="[{ required: true, message: '' }]"
            readonly
        />
        <van-popup v-model="showDatePicker" round position="bottom" :style="{ height: '40%' }">
            <van-datetime-picker
            v-model="currentDate"
            type="date"
            title=""
            :min-date="minDate"
            :max-date="maxDate"
            @confirm="handleDateComfirm"
            @cancel="handleDateCancel"
            />
        </van-popup>
      </div>
      <!--  -->
      <van-field name="self" v-model="self" label="" label-width="120">
        <template #input>
          <van-radio-group v-model="self" direction="horizontal">
            <van-radio name=""></van-radio>
            <van-radio name=""></van-radio>
          </van-radio-group>
        </template>
      </van-field>
      <!--  -->
      <div v-if="!isSelf">
        <van-field
            name="patRelation"
            v-model="patRelation"
            label=""
            label-width="120"
            placeholder=""
            @click="handleSelectRelation"
            :rules="[{ required: true, message: '' }]"
            readonly
        />
        <van-popup v-model="showRelationPicker" round position="bottom">
            <van-picker
            show-toolbar
            :columns="columns"
            @cancel="showRelationPicker = false"
            @confirm="handleConfirm"
            />
        </van-popup>
      </div>
      <!--  -->
      <van-field
        name="guardianName"
        v-model="guardianName"
        label=""
        label-width="120"
        placeholder=""
        :rules="[{ required: true, message: '' }]"
        clearable
        v-if="isChild"
      ></van-field>
      <!--  -->
      <van-field
        name="patIdNo"
        type="number"
        v-model="patIdNo"
        :label="isChild ? '' : ''"
        :placeholder="isChild ? '' : ''"
        label-width="120"
        maxlength="18"
        :rules="[{ required: true, message: '' }]"
        clearable
      />
      <!--  -->
      <van-field
        name="patPhone"
        type="number"
        v-model="patPhone"
        :label="isChild ? '' : ''"
        :placeholder="isChild ? '' : ''"
        label-width="120"
        maxlength="11"
        :rules="[{ required: true, message: '' }]"
        clearable
      />
      <!--  -->
      <div class="tips">
        <span class="strong"></span><span class="normal"></span><span class="tips-danger"></span><span class="normal"></span>
      </div>
      <div class="confirm-btn">
        <van-button round block type="info" :loading="submitLoading" loading-text="" :disabled="submitDisabled" native-type="submit"></van-button>
      </div>
    </van-form>
    <div class="bottom-tips"><span class="normal"> </span><span @click="handleNewCard" class="strong"></span></div>
  </div>
</template>
<script>
import BSwitch from "../../base/BSwitch";
export default {
    name: "BindCard",
    //
    components: {
        [BSwitch.name]: BSwitch
    },
    data() {
        return {
            isChild: false,
            patName: "",
            patSex: "1",
            minDate: new Date(1990, 0, 1),
            maxDate: new Date(2020, 10, 1),
            currentDate: new Date(),
            showDatePicker: false,
            patBirthday: "",
            self: "",
            guardianName: "",
            patRelation: "",
            showRelationPicker: false,
            columns: ["", "", "", "", "", "", ""],
            patIdNo: "",
            patPhone: "",
            submitLoading: false,
            submitDisabled: false
        };
    },
    computed: {
        userType() {
            return this.isChild ? 1 : 0;
        },
        isSelf() {
            return this.self == "";
        }
    },
    methods: {
        handleSelectDate() {
            this.closeAllPicker();
            this.showDatePicker = true;
        },
        handleSelectRelation() {
            this.closeAllPicker();
            this.showRelationPicker = true;
        },
        handleDateCancel() {
            this.showDatePicker = false;
        },
        handleDateComfirm(value) {
            let year = value.getFullYear();
            let month = (Number(value.getMonth()) + 1) + "";
            month = month.length == 1 ? ("0" + month) : month;
            let day = value.getDate() + "";
            day = day.length == 1 ? ("0" + day) : day;
            this.patBirthday = year + "-" + month + "-" + day;
            this.showDatePicker = false;
        },
        handleConfirm(value) {
            this.patRelation = value;
            this.showRelationPicker = false;
        },
        handleSubmit(values) {
            //valid the form of patIdNo and patPhone
            if (!this.validForm(values)) {
                return;
            };
            this.submitLoading = true;
            this.submitDisabled = true;
            let owner = this;
            let data = {};
            let userType = values.userType;
            data["isChild"] = userType;
            data["patRelation"] = values.patRelation || "";
            data["patName"] = values.patName;
            data["patIdNo"] = !userType ? values.patIdNo : "";
            data["patPhone"] = values.patPhone;
            data["patSex"] = values.patSex || "";
            data["patBirthday"] = values.patBirthday || "";
            data["guardianIdNo"] = userType ? values.patIdNo : "";
            data["guardianName"] = values.guardianName || "";
            data["guardianPhone"] = userType ? values.patPhone : "";
            owner.globalMethods.setRequestHeader();
            this.$axios({
                            method:'post',
                            url: owner.$store.getters.createCard,
                            data
                        }).then((response) => {
                            if (response.data.Code == 0) {
                                owner.$store.commit("binded", true);
                                owner.toastSuccess("");
                            }
                            else if(response.data.Code == -2) {
                                this.$toast({
                                    type: "fail",
                                    message: "toke",
                                    forbidClick: true,
                                    onOpened: () => {
                                        owner.globalMethods.delCookie("token");
                                        owner.$router.push("Outpatient");
                                    }
                                });
                            }
                            else {
                                owner.toastFail(response.data.Msg)
                            }
                        })
        },
        handleNewCard() {
            this.$router.replace({name: "BindCard"});
        },
        closeAllPicker() {
            this.showDatePicker = false;
            this.showRelationPicker = false;
        },
        toastFail(msg) {
            this.$toast({
                type: "fail",
                message: msg,
                forbidClick: true,
                onOpened: () => {
                    this.submitDisabled = false;
                    this.submitLoading = false;
                }
            })
        },
        toastSuccess(msg) {
            this.$toast({
                type: "success",
                message: msg,
                forbidClick: true,
                onOpened: () => {
                    this.$router.go(-1);
                }
            })
        },
        validForm(values) {
            if(!/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(values.patIdNo)) {
                this.$toast({
                    type: "fail",
                    message: "",
                    forbidClick: true
                });
                return false;
            };
            if(!/^1[3456789]\d{9}$/.test(values.patPhone)) {
                this.$toast({
                    type: "fail",
                    message: "",
                    forbidClick: true
                });
                return false;
            };
            return true;
        }
    }
};
</script>
<style scoped>
.user-type {
    margin-bottom: 20px;
}
.tips {
    padding: 10px;
    font-size: 12px;
    text-align: left;
    color: gray;
    line-height: 17px;
}
.strong {
    color: blue;
}
.tips-danger {
    color: red;
}
.confirm-btn {
  margin: 16px;
}
.bottom-tips {
    font-size: 12px;
    text-align: right;
    padding: 10px;
    margin: 10px;
    height: 20px;
    line-height: 20px;
}
</style>



