<template>
    <div class="main-bg">
        <div>
            <van-cell-group class="card-item radius-10">
                <van-cell title-style="text-align: left;" title="" :value="dataSource.inHosCerNo" />
                <van-cell title-style="text-align: left;" title="" :value="dataSource.patName" />
                <van-cell title-style="text-align: left;" title="" :value="dataSource.patSex == 1 ? '' : ''" />
                <van-cell title-style="text-align: left;" title="" :value="dataSource.patAge + ''" />
                <van-cell title-style="text-align: left;" title="" :value="dataSource.patIdNo" />
                <van-cell title-style="text-align: left;" title="" :value="dataSource.deptName"/>
                <van-cell title-style="text-align: left;" title="" :value="dataSource.diagnosis"/>
                <van-cell title-style="text-align: left;" title="" :value="dataSource.inHosDate"/>
                <van-cell title-style="text-align: left;" title="" :value="dataSource.patPhone"/>
            </van-cell-group>
            <div class="confirm-btn">
                <van-button round block type="info" :loading="submitLoading" loading-text="" :disabled="submitDisabled" native-type="submit" @click="handleComfirm"></van-button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: "InHosRegisterInfo",
    data() {
        return {
            dataSource: "",
            submitLoading: false,
            submitDisabled: false
        };
    },
    mounted() {
        this.dataSource = this.$route.params.data;
    },
    methods: {
        handleComfirm() {
            this.$toast.loading({message: '...', forbidClick: true, duration: 0});
            let owner = this;
            owner.globalMethods.setRequestHeader();
            this.$axios({
                    method:'post',
                    url: owner.$store.getters.inHospitalRegister,
                    data: {
                       inHosCerNo: owner.dataSource.inHosCerNo
                    }
                }).then ((response) => {
                    if (response.data.Code == 0) {
                        owner.$store.commit("bindedInHosNo", true);
                        owner.$toast.clear();
                        owner.$router.go(-1);
                    }
                    else if (response.data.Code == -2) {
                            owner.$toast({
                                type: "fail",
                                message: "toke",
                                forbidClick: true,
                                onOpened: () => {
                                    owner.globalMethods.delCookie("token");
                                    owner.$router.push("Outpatient");
                                }
                            });
                    }
                    else {
                        owner.$toast({
                                type: "fail",
                                message: response.data.Msg,
                                forbidClick: true
                            })
                    }
                });
        }
    }
};

</script>
<style scoped>
.card-item {
    margin-bottom: 15px;
}
.confirm-btn {
    margin: 30px;
}
</style>



